@media (max-width: 800px) {
  .active {
    background-color: rgb(223, 168, 51);
    padding: 15px;
    color: #333;
    outline: none;
    transition: 0.1s;
    box-shadow: 0 0 0 3px #fea, 0 0 0 4px #f4b900;
    border-radius: 5px;
  }

  .inputselect {
    min-height: 50px;
    border-radius: 2px;
    font-size: 0.7rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: rgba(0, 0, 0, 0.055) 0px 1px 3px 0px,
      rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;

    cursor: pointer;
    margin-top: 15px;

    transition: 0.1s;
  }

  .inputselect:hover {
    background-color: rgb(111, 123, 230);
    color: rgb(250, 250, 250);
  }

  .inputselect.selected {
    background-color: rgb(111, 123, 230);
    color: white;
  }

  .inputselect span {
    background-color: rgb(56, 15, 206);
    height: 50px;
    justify-content: center;
    align-items: center;
    display: flex;
    color: white;
    width: 20px;
    border-radius: 2px;
  }
}
